import Vue from 'vue'
import Router from 'vue-router'
import movieRouter from './movie'
import mineRouter from './mine'
import cinemaRouter from './cinema'

Vue.use(Router)
export  default new Router({

  //base:process.env.EASE_URL,
  routes:[
   movieRouter,
    mineRouter,
    cinemaRouter,
    {
      path:'/',
      redirect:movieRouter
    }
  ]
})



